// Studio Search Section - お近くのフォトスタジオを探す
// =============================================================================

@use '../../base/mixins' as m;
@use '../../base/variables' as v;

.studio-search-section {
	width: 100%;
	padding: m.fs(72, 0.6) 0;

	&__container {
		margin: 0 m.fs(224, 0.4);
		padding-top: m.fs(72, 0.6);
		padding-bottom: m.fs(72, 0.6);
		background: #e9edf0;
		display: flex;
		flex-direction: column;
		align-items: center;
		position: relative;
		z-index: 0;
	}

	// ヘッダーエリア
	&__header {
		text-align: center;
		margin-bottom: m.fs(40, 0.5);
	}

	&__label {
		display: flex;
		justify-content: center;
		margin-bottom: m.fs(64, 0.5);
	}

	&__title {
		font-size: m.fs(28, 0.6);
		font-weight: 600;
		color: v.$main-color;
		margin-bottom: m.fs(48, 0.5);
		font-family: v.$ja;
		letter-spacing: m.fs(1);
	}

	&__subtitle {
		font-size: m.fs(14, 0.4);
		color: v.$main-color;
		font-family: v.$ja-sans;
	}

	// 検索エリア
	&__search {
		width: m.fs(576, 0.7);
		margin-bottom: m.fs(60, 0.5);
		display: flex;
		justify-content: center;
	}

	&__search-box {
		position: relative;
		width: 100%;
		padding: m.fs(16, 0.5) m.fs(32, 0.5);
		display: flex;
		align-items: center;
		background: #fff;
		border: 2px solid #ddd;
		border-radius: m.fs(8, 0.3);

		&:focus-within {
			border-color: #666;
		}
	}

	&__search-icon {
		width: m.fs(20, 0.4);
		height: m.fs(20, 0.4);
		fill: #666;
		margin-left: m.fs(12, 0.4);
		margin-right: m.fs(8, 0.3);
		flex-shrink: 0;
	}

	&__search-input {
		flex: 1;
		border: none;
		outline: none;
		font-size: m.fs(14, 0.4);
		font-family: v.$ja-sans;
		background: transparent;
		padding: m.fs(12, 0.4) m.fs(12, 0.4);

		&::placeholder {
			color: #999;
		}
	}

	// カードエリア
	&__cards {
		width: calc(100% + m.fs(128, 0.5));
		display: grid;
		grid-template-columns: repeat(3, 1fr);
		gap: m.fs(24, 0.4);
		margin-bottom: m.fs(60, 0.5);
	}

	// ページネーション
	&__pagination {
		display: flex;
		justify-content: center;
		align-items: center;
		gap: m.fs(16, 0.4);
	}
}

// スタジオカード
.studio-card {
	display: grid;
	grid-template-rows: auto 1fr;
	background: #fff;
	min-height: m.fs(468, 0.6);
	border-radius: m.fs(10, 0.5);
	padding: m.fs(16, 0.5);
	text-align: center;
	overflow: visible;
	position: relative;
	z-index: 1;
	box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.1);

	&__image {
		position: relative;
		width: 100%;
		height: m.fs(160, 0.6);
		background: #e0e0e0;
		overflow: hidden;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__location {
		position: absolute;
		bottom: 0;
		right: 0;
		background: #fff;
		color: #a99f3c;
		padding: m.fs(4, 0.3) m.fs(12, 0.3);
		border-radius: m.fs(4, 0.3) 0 0 0;
		font-size: m.fs(16, 0.6);
		font-family: v.$ja-sans;
	}

	&__content {
		display: grid;
		grid-template-rows: auto 1fr auto;
		gap: m.fs(12, 0.4);
		padding: m.fs(16, 0.4);
	}

	&__name {
		font-size: m.fs(18, 0.5);
		font-weight: 400;
		color: #333;
		font-family: v.$ja-sans;
		letter-spacing: m.fs(1);
	}

	&__address {
		font-size: m.fs(16, 0.6);
		text-align: left;
		color: #666;
		line-height: 1.5;
		font-family: v.$ja-sans;
	}

	&__details {
		font-size: m.fs(16, 0.6);
		display: flex;
		flex-direction: column;
		gap: m.fs(8, 0.3);
	}

	&__hours {
		// studio-card__detailsに含まれる
		font-size: m.fs(16, 0.6);
	}

	&__hour-item {
		font-size: m.fs(16, 0.6);
		color: #666;
		line-height: 1.4;
		font-family: v.$ja-sans;
		text-align: left;
	}

	&__contact-btn {
		// camera-buttonコンポーネントを使用するため、独自スタイルは削除
		width: 100%;
		display: flex;
		justify-content: center;
		overflow: visible;
		padding: m.fs(8, 0.3);

		.camera-button {
			width: 100%;
		}
	}
}

// ページネーション
.pagination-btn {
	background: transparent;
	border: 1px solid v.$brand-gold;
	padding: m.fs(10, 0.4) m.fs(16, 0.5);
	border-radius: m.fs(10);
	cursor: pointer;
	transition: all 0.3s ease;
	position: relative;
	font-family: v.$ja-sans;
	font-size: m.fs(14);
	color: v.$brand-gold;
	font-weight: 500;

	// &::before {
	// 	content: '';
	// 	position: absolute;
	// 	top: m.fs(3);
	// 	left: m.fs(3);
	// 	right: m.fs(-3);
	// 	bottom: m.fs(-3);
	// 	background: #fff;
	// 	border-radius: m.fs(20);
	// 	z-index: -1;
	// 	border: 1px solid v.$brand-gold;
	// }

	&:hover {
		background: v.$brand-gold;
		color: #fff;
	}

	&--prev,
	&--next {
		font-size: m.fs(14);
		padding: m.fs(10, 0.4) m.fs(14, 0.5);
	}
}

.pagination-numbers {
	display: inline-flex;
	align-items: center;
	gap: m.fs(8, 0.3);
	font-size: m.fs(14);
	color: v.$main-color;
	font-family: v.$ja-sans;

	span {
		display: inline-flex;
		align-items: center;
		justify-content: center;
		width: m.fs(32, 0.5);
		height: m.fs(32, 0.5);
		border-radius: 50%;
		cursor: pointer;
		transition: all 0.3s ease;

		&:hover {
			background: v.$brand-gold;
			color: #fff;
		}

		&.active {
			background: v.$brand-gold;
			color: #fff;
		}
	}
}
